.bm-list {
    padding: 10px;
    margin: auto;
}

.bm-list li {
    display: block;
    color: #ffffff;
    padding: 16px;
    font-weight: 500;
    text-decoration: none;
}

.bm-title {
    text-align: center;
    color: #ffffff;
    margin: 5px;
}

.bm-form {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.bm-form input {
    width: 80%;
    justify-self: center;
    align-self: center;
}

/* button styles from https://www.bestcssbuttongenerator.com/ */
.search-filter {
	box-shadow:inset 0px 1px 3px 0px #91b8b3;
	background:linear-gradient(to bottom, #99aab5 5%, #7289da 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#99aab5', endColorstr='#7289da',GradientType=0);
	background-color:#99aab5;
	border-radius:5px;
	border:1px solid #566963;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:5px 11px;
	text-decoration:none;
	text-shadow:0px -1px 0px #2b665e;
}

.search-box {
    float: none;
    border: none;
    margin: 16px;
    padding: 6px;
    font-size: 17px;
    display: block;
    text-align: left;
    width: 80%;
    border: 1px solid #cccccc;
    border-radius: 2px;
}
.map {
    display: inline-block;
    width: 100%;
    height: 100vh;
    vertical-align: top;
}

/* infowindow container */
/* mapVenueTooltip rightRatingLayout*/
.infowindow {
    background: #fff;
    border: 2px solid #2d5be3;
    font-size: 13px;
    line-height: 16px;
    padding: 10px;
    position: relative;
    text-align: left;
    border-radius: 3px;
    cursor: pointer;
    width: 307px;
    /* To fix issue caused by maps moving exit button */
    left: 0 !important;
}

.infowindow-inside-container {
    margin: 0;
    overflow: hidden;
}

.infowindow-icon {
    border-radius: 3px;
    background: #efeff4;
    overflow: hidden;
    margin: 0 10px 10px 0;
    height: 80px;
    width: 80px;

    float: left;
    line-height: 0;
    position: relative;
}

.infowindow-icon img {
    height: 80px;
    width: 80px;
}

.infowindow-details {
    width: 223px;
    float: left;
}

.infowindow-name {
    margin: 3px 0;
}

.infowindow-name a {
    font-weight: 500;
    color: #2d5be3;
    font-size: 15px;
    text-decoration: none;
}

.infowindow-name a:hover {
    text-decoration: underline;
}

.infowindow-rating {
    margin: 3px 0;
    text-align: center;
}

.hours, .infowindow-address, .infowindow-phone {
    font-size: 0.7rem;
}

.infowindow-address-data {
    min-height: inherit;
    width: inherit;
}

/* remove white background and shadow of infowindow */
.infowindow-parent div:nth-child(even) {
    display: none;
}

button .gm-ui-hover-effect {
    width: 40px;
    height: 40px;
}

.infowindow-description-container {
    padding: 5px 0;
}

.restaurant-url {
    text-decoration: none;
    font-weight: 400;
    width: 260px;
}

.restaurant-description, .hours {
    width: 90%;
}
/* Aria */

.aria-labels {
    display: none;
}
body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
  
